import React, { Children, useState } from 'react'
import Web3 from 'web3'
import { useSITContract, useUserAccount } from '../../../contracts/hooks'
import { toast } from "react-toastify";
import Loader from '../loader/Loader'
import close from '../../../assets/images/staking/close.png'
import Bthlogo from '../../../assets/images/cards/headerlogo.svg'
// import { crossIcon } from '../icons';
import {
  ModalBody,
  ModalContent,
  CardContainer,
  CardBackground,
  CloseIconWrapper,
  HeaderLayout,
  CryptoIconWrapper,
  HeaderText,
  HeaderContent,
  HeaderButtonWrapper,
  APRButton,
  DaysButton,
  WithdrawDepositTabsLayout,
  WithdrawDepositTabsContainer,
  DepositTab,
  WithdrawTab,
  Row3,
  Rightsinglebox,
  RightSideContent1,
  RightSideContent2,
  Leftsinglebox,
  LeftSideContent1,
  LeftSideContent2,
  Duration,
  Stakeduration,
  InnerbuttonWrapper,
  Button,
  Buttoncontain,
  WithDrawBTCInputContainer,
  InputField,
  WithdrawNowButtonWrapper,
  WithDrawNowButton,
  Maxwrapper,
  Maxcontain,
  Headlinewrapper,
  Headline,
  Row1wrapper,
  InnerWrapperRow1,
  Leftwrapper,
  Logoicon,
  StakeandBTcwrapper,
  Stakeasset,
  BTCvalue,
  RightWrapper,
  Aprcontainer,
  AprContain,
  Dayswrapper,
  DaysContain,
  InputWrapper,
  InputUpperWrapper,
  Singleinput,
  RightSideContent,
  LeftsideContent,
  BottomSideContainer,
  BottomSideText,
  BottomCenterText
} from './Confirmstakingstyle'
export const Confirmstaking = (props: any) => {
  const {days, show , toggleModal, onNext ,onClose,amount,setAmount} = props
  const handleClickOutside = (e: any) => {
    if (e.target === e.currentTarget) {
      toggleModal()
    }
  }
  // const [amount, setAmount] = useState(props.amount);
  
  const [loading, setLoading] = useState(false);
  const {account}=useUserAccount();

  const StakeInitializableInstance=useSITContract()
  
  const depositStake = async(e:any) => {
   
    try{
      e.preventDefault();
      setLoading(true);
      console.log("instance", StakeInitializableInstance);
      
      console.log("amount", amount);
      
      const weiAmount = Web3.utils.toWei(amount.toString(), "ether");
      const reponse = await StakeInitializableInstance?.methods?.deposit(weiAmount,days ).send({
    from:account
      }) 
      if(reponse){
        setLoading(false);
        // Success toaster

       
        toast.success("Tokens Deposited")
        // Close Modal
      }
      onNext(3)
      console.log(reponse);
    }catch(e){
      // Error Toaster
      // alert(e.message);
      setLoading(false);
    }
    
  }
  return (
    <CardBackground show={show} onMouseDown={handleClickOutside}>
          <Loader open={loading}/>
      <CardContainer>
        <CloseIconWrapper onClick={()=> onClose() }>
          <img height="19px" width="19px" src={close} alt="close" />
        </CloseIconWrapper>
        <Headlinewrapper>
          <Headline>Confirm Staking</Headline>
        </Headlinewrapper>
        <Row1wrapper>
          <InnerWrapperRow1>
            <Leftwrapper>
              <Logoicon src={Bthlogo} />
              <StakeandBTcwrapper>
                <Stakeasset>Stake asset</Stakeasset>
                <BTCvalue>0.5039 BTC</BTCvalue>
              </StakeandBTcwrapper>
            </Leftwrapper>
            <RightWrapper>
              <Aprcontainer>
                <AprContain>32.65% APR</AprContain>
              </Aprcontainer>

              <Dayswrapper>
                <DaysContain>{days} days</DaysContain>
              </Dayswrapper>
            </RightWrapper>
          </InnerWrapperRow1>
        </Row1wrapper>
        <InputWrapper>
          <InputUpperWrapper>
            <Singleinput>
              <RightSideContent>Maturity Date</RightSideContent>
              <LeftsideContent>12 JAN 2022 12:00 UTC</LeftsideContent>
            </Singleinput>
         
            <Singleinput>
              <RightSideContent>Network Fee</RightSideContent>
              <LeftsideContent>0.0000 BTC</LeftsideContent>
            </Singleinput>
          </InputUpperWrapper>
          <WithdrawNowButtonWrapper>
          <WithDrawNowButton onClick={(e: any) => depositStake(e)}  disabled={loading}   > {loading?"Confirm Staking" : "Confirm and Execute"}</WithDrawNowButton>
        </WithdrawNowButtonWrapper>
        </InputWrapper>
        <BottomSideContainer>
          <BottomSideText>
          Staking will start immediately after clicking "Confirm and Execute".
          </BottomSideText>
          <BottomCenterText>
          * You may unstake your tokens at any time. There is an unstaking period of a month you must wait before withdrawing your staked tokens.

          </BottomCenterText>
        </BottomSideContainer>

      </CardContainer>
    </CardBackground>
  )
}
